import { jsx as _jsx } from "react/jsx-runtime";
import ReactDOM from "react-dom";
import { App } from "./App";
var init = null;
var app = {
    config: function (config) {
        init = config;
    },
    widgets: {
        myWidget: {
            new: function () {
                return {
                    render: function () {
                        ReactDOM.render(_jsx(App, { widget: init.widget, entity: init.entity }), document.querySelector(init.selector));
                    },
                    unmount: function () {
                        ReactDOM.unmountComponentAtNode(document.querySelector(init.selector));
                    },
                };
            }
        }
    }
};
